import Flickity from "flickity";
import pegasus from "../../assets/scripts/global/01-pegasus";

export default class ImageChangeTextSlider {
    image = "";

    /**
     * @var {array}
     */
    slides = null;

    /**
     * @var {HTMLElement}
     */
    slider = null;

    /**
     * @var {HTMLElement}
     */
    sliderContainer = null;

    /**
     * @var {int}
     */
    screenWidth = 0;

    /**
     * @var {object}
     *  
     */
    options = {};

    constructor(image) {
        this.image = image;
    }

    init() {
        console.log('[ImageChangeTextSlider] init() called', {
            hasRoot: !!this.$root,
            rootElement: this.$root?.tagName
        });
        this.initSlider();
    }

    initSlider() {
        console.log('[ImageChangeTextSlider] initSlider() called');
        
        this.sliderContainer = this.$root.querySelector(".ImageChangeTextSlider__slider");
        console.log('[ImageChangeTextSlider] sliderContainer found:', !!this.sliderContainer, {
            container: this.sliderContainer?.tagName,
            classes: this.sliderContainer?.className
        });
        
        if (!this.sliderContainer) {
            console.error('[ImageChangeTextSlider] ERROR: sliderContainer not found!');
            return;
        }
        
        // Check if Flickity is already initialized
        const existingFlickity = this.sliderContainer._flickity || this.sliderContainer.flickity;
        console.log('[ImageChangeTextSlider] existingFlickity check:', !!existingFlickity);
        
        if (existingFlickity) {
            console.log('[ImageChangeTextSlider] Flickity already initialized, resizing...');
            // Already initialized, just resize
            existingFlickity.resize();
            return;
        }
        
        // CRITICAL: Clean up any existing Flickity DOM structure
        // Flickity creates viewport and slider divs, but if they already exist from a previous init,
        // we need to remove them and let Flickity create fresh ones
        const existingViewport = this.sliderContainer.querySelector('.flickity-viewport');
        const existingFlickitySlider = this.sliderContainer.querySelector('.flickity-slider');
        
        console.log('[ImageChangeTextSlider] Cleaning up existing Flickity structure...', {
            hasViewport: !!existingViewport,
            hasFlickitySlider: !!existingFlickitySlider
        });
        
        // Get all slides before cleanup
        const allSlides = this.sliderContainer.querySelectorAll('.slide');
        console.log('[ImageChangeTextSlider] Found slides before cleanup:', allSlides.length);
        
        // If there's an existing viewport structure, we need to move slides back to container
        // and remove the Flickity-created DOM
        if (existingViewport || existingFlickitySlider) {
            // Move all slides directly to container (Flickity will create its own structure)
            allSlides.forEach(slide => {
                if (slide.parentElement !== this.sliderContainer) {
                    this.sliderContainer.appendChild(slide);
                    console.log('[ImageChangeTextSlider] Moved slide to container');
                }
            });
            
            // Remove Flickity-created DOM
            if (existingViewport) {
                existingViewport.remove();
                console.log('[ImageChangeTextSlider] Removed existing viewport');
            }
            if (existingFlickitySlider && existingFlickitySlider.parentElement !== this.sliderContainer) {
                existingFlickitySlider.remove();
                console.log('[ImageChangeTextSlider] Removed existing flickity-slider');
            }
        }
        
        this.slides = this.sliderContainer.querySelectorAll(".slide");
        console.log('[ImageChangeTextSlider] slides found:', this.slides?.length || 0);
        
        if (!this.slides || this.slides.length === 0) {
            console.error('[ImageChangeTextSlider] ERROR: No slides found!');
            return;
        }
        
        this.screenWidth = window.innerWidth; // Changed from outerWidth
        console.log('[ImageChangeTextSlider] screenWidth:', this.screenWidth);

        // Temporarily reset slide positioning so Flickity can measure them
        // Only reset if slides are already absolutely positioned
        console.log('[ImageChangeTextSlider] Resetting slide positioning...');
        this.slides.forEach((slide, index) => {
            if (!slide) {
                console.warn(`[ImageChangeTextSlider] Slide ${index} is null/undefined`);
                return;
            }
            
            if (!slide.style) {
                console.warn(`[ImageChangeTextSlider] Slide ${index} has no style property`, slide);
                return;
            }
            
            try {
                const currentPosition = window.getComputedStyle(slide).position;
                console.log(`[ImageChangeTextSlider] Slide ${index} position:`, currentPosition);
                
                // Only reset if already absolutely positioned
                if (currentPosition === 'absolute') {
                    slide.style.position = 'relative';
                    slide.style.left = 'auto';
                    slide.style.transform = 'none';
                    slide.style.width = 'auto';
                    console.log(`[ImageChangeTextSlider] Slide ${index} reset to relative`);
                }
            } catch(e) {
                console.error(`[ImageChangeTextSlider] Error processing slide ${index}:`, e);
            }
        });

        this.options = {
            cellAlign: "left",
            contain: true,
            wrapAround: false,
            prevNextButtons: false,
            pageDots: false,
            draggable: true,
            percentPosition: false,
            freeScroll: false,
            resize: false,
            on: {
                change: (i) => {
                    if (this.slider && this.slider.selectedElement) {
                        this.slider.selectedElement.click();
                    }
                },
            },
        };

        (window.outerWidth > 768) ? this.options.draggable = false : this.options.draggable = true;
        console.log('[ImageChangeTextSlider] Options configured:', {
            draggable: this.options.draggable,
            cellAlign: this.options.cellAlign
        });
        
        // Use global Flickity (exposed from main.js)
        // This ensures we use the same Flickity instance that's available globally
        console.log('[ImageChangeTextSlider] Checking for window.Flickity...', {
            flickityType: typeof window.Flickity,
            flickityIsFunction: typeof window.Flickity === 'function',
            flickityExists: typeof window.Flickity !== 'undefined'
        });
        
        if (typeof window.Flickity === 'undefined') {
            console.error('[ImageChangeTextSlider] ERROR: Flickity is not available globally. Make sure it is exposed in main.js');
            return;
        }
        
        console.log('[ImageChangeTextSlider] About to create Flickity instance...', {
            container: this.sliderContainer,
            options: this.options
        });
        
        try {
            // Initialize Flickity - it will now measure and position slides correctly
            this.slider = new window.Flickity(this.sliderContainer, this.options);
            console.log('[ImageChangeTextSlider] SUCCESS: Flickity instance created!', {
                slider: this.slider,
                cells: this.slider?.cells?.length || 0
            });
        } catch(e) {
            console.error('[ImageChangeTextSlider] ERROR creating Flickity instance:', e);
            console.error('[ImageChangeTextSlider] Error stack:', e.stack);
            throw e;
        }
        
        // Flickity will handle positioning, so we don't need to restore original styles


        this.slider.on('ready', () => this.slider.resize());

        let resizeTimeout;
        pegasus.onResizeFinished(() => {
            // Clear any existing timeout
            clearTimeout(resizeTimeout);
            
            // Set a new timeout
            resizeTimeout = setTimeout(() => {
                let newWidth = window.innerWidth; // Changed from outerWidth
                // Only resize if the width actually changed
                if (Math.abs(newWidth - this.screenWidth) > 10) { // Added threshold
                    if(this.slider) {
                        this.slider.destroy();
                    }
                    (window.outerWidth > 768) ? this.options.draggable = false : this.options.draggable = true;
                    this.screenWidth = newWidth;
                    this.slider = new Flickity(this.sliderContainer, this.options);
                }
            }, 150); // Added debounce delay
        });
        
        // Improve touch handling for iOS
        if ((/iPad|iPhone|iPod/.test(navigator.platform) || 
            (navigator.platform === 'MacIntel' && navigator.maxTouchPoints > 1))) {
            this.sliderContainer.style.touchAction = 'pan-y pinch-zoom';
            document.body.style.overscrollBehaviorX = 'none';
        }

    }
}

document.addEventListener("alpine:init", () => {
    Alpine.data(
        "imageChangeTextSlider",
        (image) => new ImageChangeTextSlider(image)
    );
});
